
import React, { Component, useState } from 'react'
import Webcam from "react-webcam";
import defaultImg from '../../assets/imgs/default.png'
import './WebcamCapture.scss'
import gorilla from '../../assets/glb/RobotExpressive.glb'


export function WebcamCapture() {
    var videoConstraintsDefault = {
        width: 1280,
        height: 720,
        // facingMode: { exact: "environment" }
        facingMode: "user"
    };
    const webcamRef = React.useRef(null);
    const [url, setUrl] = useState(defaultImg);
    const [videoConstraints, setVideoConstraints] = useState(videoConstraintsDefault)
    const changeCamera = () => {
        if (videoConstraints.facingMode === "user") {
            setVideoConstraints({ videoConstraints, facingMode: { exact: "environment" } })
        } else {
            setVideoConstraints({ videoConstraints, facingMode: "user" })
        }
    }
    const capture = React.useCallback(
        async () => {
            const imageSrc = webcamRef.current.getScreenshot();
            await setUrl(imageSrc)
        },
        [webcamRef]
    );

    return (
        <div className="webcam-capture">
            <div className="btns">
                <button onClick={capture}>Capture photo</button>
                <button onClick={changeCamera}>Change Camera</button>
            </div>
            <div className="container">
                <div className="camera">
                    <Webcam
                        audio={false}
                        ref={webcamRef}
                        screenshotFormat="image/jpeg"
                        videoConstraints={videoConstraints}
                    />
                    <div className="demo">
                        <model-viewer src={gorilla} auto-rotate camera-controls>
                        </model-viewer>
                    </div>
                </div>
                <div className="container-screen-shot">
                    <img className="screen-shot" src={url} alt="" />
                    <div className="demo">
                        <model-viewer src={gorilla} auto-rotate camera-controls >
                        </model-viewer>
                    </div>
                </div>
            </div>
        </div>
    );
};
